import { Spinner } from '@/components/Spinner';
import { useAuth } from '@/features/auth/contexts';
import { useLaunchDarkly } from '@/hooks';
import { Stack } from '@/layout';
import EditChamber from '../Chamber/EditChamber/EditChamber';
import EditNoum from '../Chamber/EditChamber/EditNoum';

const EditHomeNoum = (): JSX.Element => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const { masterId, loading } = useAuth();
  return (
    <>
      {loading && !masterId ? (
        <Stack>
          <Spinner />
        </Stack>
      ) : noumEditor2 ? (
        <EditNoum id={masterId} />
      ) : (
        <EditChamber id={masterId} />
      )}
    </>
  );
};

export default EditHomeNoum;
